{isEditing && editProduct && (
  <div className="modal">
    <form onSubmit={handleUpdate}>
      {/* Tên sản phẩm */}
      <label>
        Tên sản phẩm:
        <input
          type="text"
          value={editProduct.name || ""}
          onChange={(e) => setEditProduct({ ...editProduct, name: e.target.value })}
          required
        />
      </label>

      {/* Giá sản phẩm */}
      <label>
        Giá:
        <input
          type="number"
          value={editProduct.price || 0}
          onChange={(e) => setEditProduct({ ...editProduct, price: parseInt(e.target.value, 10) })}
          required
        />
      </label>

      {/* Số lượng sản phẩm */}
      <label>
        Số lượng:
        <input
          type="number"
          value={editProduct.stock || 0}
          onChange={(e) => setEditProduct({ ...editProduct, stock: parseInt(e.target.value, 10) })}
          required
        />
      </label>

      {/* Danh mục */}
      <label>
        Danh mục:
        <select
          value={editProduct.category?._id || ""}
          onChange={(e) => {
            const selectedCategory = categories.find((cat) => cat._id === e.target.value);
            setEditProduct({
              ...editProduct,
              category: selectedCategory || undefined,
            });
          }}
          required
        >
          <option value="">Chọn danh mục</option>
          {categories.map((category) => (
            <option key={category._id} value={category._id}>
              {category.name}
            </option>
          ))}
        </select>
      </label>

      {/* Mô tả */}
      <label>
        Mô tả:
        <textarea
          value={editProduct.description || ""}
          onChange={(e) => setEditProduct({ ...editProduct, description: e.target.value })}
        />
      </label>

      {/* Thành phần */}
      <label>
        Thành phần:
        <textarea
          value={editProduct.ingredients?.join("\n") || ""}
          onChange={(e) =>
            setEditProduct({ ...editProduct, ingredients: e.target.value.split("\n") })
          }
        />
      </label>

      {/* Hướng dẫn sử dụng */}
      <label>
        Hướng dẫn sử dụng:
        <textarea
          value={editProduct.usage_instructions?.join("\n") || ""}
          onChange={(e) =>
            setEditProduct({ ...editProduct, usage_instructions: e.target.value.split("\n") })
          }
        />
      </label>

      {/* Đặc điểm nổi bật */}
      <label>
        Đặc điểm nổi bật:
        <textarea
          value={editProduct.special?.join("\n") || ""}
          onChange={(e) =>
            setEditProduct({ ...editProduct, special: e.target.value.split("\n") })
          }
        />
      </label>

      {/* Hình ảnh */}
      <label>
        Hình ảnh:
        <input
          type="file"
          multiple
          onChange={(e) => {
            const files = e.target.files;
            if (files) {
              setEditProduct({
                ...editProduct,
                newImages: Array.from(files),
              });
            }
          }}
        />
      </label>

      <button type="submit">Lưu</button>
      <button type="button" onClick={() => setIsEditing(false)}>
        Hủy
      </button>
    </form>
  </div>
)}